.footerWrapper {
    display: flex;
    justify-content: center;
    width: 100%;
    background: linear-gradient(
        0deg,
        rgba(2, 0, 36, 1) 50%,
        rgba(255, 115, 0, 0.85) 100%
    );
    height: 4rem;
}
.text {
    color: white;
    font-family: 'PF DinDisplay Medium', 'Trebuchet MS', Arial;
    font-size: 1rem;
}

@media (min-width: 1200px) {
    .footerWrapper {
        max-width: 1200px;
        position: fixed;
        bottom: 0%;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: flex-end;
        background: linear-gradient(
            0deg,
            rgba(2, 0, 36, 1) 50%,
            rgba(255, 115, 0, 0.85) 100%
        );
        height: 4rem;
        z-index: 10000;
    }
}
