.searchForm {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    margin-top: 0.7%;
    margin-bottom: 0.7%;
    background: linear-gradient(
        180deg,
        rgba(2, 0, 36, 1) 50%,
        rgba(255, 115, 0, 0.85) 100%
    );
}
.searchInput {
    background-color: transparent !important;
    border: none;
    /* -webkit-user-select: none; */
    /* -ms-user-select: none; */
    color: #fff;
    /* user-select: none; */
    outline: none;
    padding-left: 3%;
}

.searchBtn {
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    background-color: transparent;
    outline: none;
    cursor: pointer;
}

.searchBtnImg {
    fill: gray;
    width: 1em;
    height: 1em;
    display: inline-block;
    font-size: 1.5rem;
    flex-shrink: 0;
    user-select: none;
}
