.wrapHeader {
    position: fixed;
    width: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    background: linear-gradient(
        180deg,
        rgba(2, 0, 36, 1) 50%,
        rgba(255, 115, 0, 0.85) 100%
    );
    height: 4rem;
    padding: 0.2rem;
    z-index: 10000;
}

.wrapNav {
    display: flex;
    list-style-type: none;
    justify-content: center;
    padding: 0;
    width: 50%;
}

.HeaderMenu {
    text-decoration: none;
    color: white;
    font-family: 'PF DinDisplay Medium', 'Trebuchet MS', Arial;
    font-size: 1.1rem;
    margin-left: 1rem;
    padding-bottom: 1rem;
    display: flex;
    flex-direction: column;
}

.HeaderMenuHome {
    margin-bottom: 1rem;
}

.HeaderMenuHome > a {
    text-decoration: none;
    color: white;
    font-family: 'PF DinDisplay Medium', 'Trebuchet MS', Arial;
    font-size: 1.1rem;
    margin-left: 1rem;
}

.HeaderMenuHome:hover,
.HeaderMenu:hover {
    text-decoration: none;
    text-shadow: 0 1px 0 rgb(255 255 255 / 10%),
        0 0 10px rgb(255 255 255 / 100%);
}

.hiddenHeaderMenu > a {
    text-decoration: none;
    color: white;
    font-family: 'PF DinDisplay Medium', 'Trebuchet MS', Arial;
    font-size: 1rem;
    padding: 10%;
}

.menuIsActiv {
    text-shadow: 0 1px 0 rgb(255 255 255 / 10%),
        0 0 10px rgb(255 255 255 / 100%);
}
.GengeMenu {
    text-decoration: none;
    color: white;
    font-family: 'PF DinDisplay Medium', 'Trebuchet MS', Arial;
    font-size: 1.1rem;
    margin-left: 1rem;
    padding-bottom: 2rem;
    cursor: pointer;
    flex-direction: column;
}
.hiddenGengeMenu {
    display: none;
}
.GengeMenu:hover {
    text-shadow: 0 1px 0 rgb(255 255 255 / 10%),
        0 0 10px rgb(255 255 255 / 100%);
}
.GengeMenu:hover .hiddenGengeMenu {
    display: flex;
    text-align: left;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    background: #2f364a;
    border-radius: 3%;
    width: 8rem;
    overflow: scroll;
}
.hiddenGengeMenu:hover {
    display: flex;
}

/* Large desktops and laptops */
@media (min-width: 1200px) {
    .wrapHeader {
        position: fixed;
        display: flex;
        justify-content: center;
        flex-direction: row;
        width: 1200px;
        background: linear-gradient(
            180deg,
            rgba(2, 0, 36, 1) 50%,
            rgba(255, 115, 0, 0.85) 100%
        );
        height: 50px;
        padding: 0;
        z-index: 10000;
    }
    .wrapNav {
        display: flex;
        list-style-type: none;
        justify-content: flex-start;
        padding: 0;
        width: 50%;
    }
    .HeaderMenu {
        text-decoration: none;
        color: white;
        font-family: 'PF DinDisplay Medium', 'Trebuchet MS', Arial;
        font-size: 1.1rem;
        margin-left: 1rem;
        padding-bottom: 2rem;
    }
    .HeaderMenuHome > a {
        text-decoration: none;
        color: white;
        font-family: 'PF DinDisplay Medium', 'Trebuchet MS', Arial;
        font-size: 1.1rem;
        margin-left: 1rem;
    }
    .HeaderMenuHome:hover,
    .HeaderMenu:hover {
        text-decoration: none;
        text-shadow: 0 1px 0 rgb(255 255 255 / 10%),
            0 0 10px rgb(255 255 255 / 100%);
        top: 5rem;
    }

    .hiddenHeaderMenu {
        display: none;
    }

    .HeaderMenu:hover .hiddenHeaderMenu {
        display: flex;
        flex-direction: column;
        justify-content: center;
        text-align: center;
        background-color: rgba(2, 0, 36, 1);
        border-radius: 5px;
    }

    .menuIsActiv {
        text-shadow: 0 1px 0 rgb(255 255 255 / 10%),
            0 0 10px rgb(255 255 255 / 100%);
    }
    .GengeMenu {
        text-decoration: none;
        color: white;
        font-family: 'PF DinDisplay Medium', 'Trebuchet MS', Arial;
        font-size: 1.1rem;
        margin-left: 1rem;
        padding-bottom: 2rem;
        cursor: pointer;
    }
    .GengeMenu:hover {
        text-shadow: 0 1px 0 rgb(255 255 255 / 10%),
            0 0 10px rgb(255 255 255 / 100%);
    }
    .hiddenGengeMenu {
        display: none;
    }
    .GengeMenu:hover .hiddenGengeMenu {
        display: flex;
        flex-direction: column;
        justify-content: center;
        text-align: center;
        background: rgba(2, 0, 36, 1) 50%;
        border-radius: 10px;
        width: 9rem;
        padding: 1%;
        overflow: hidden;
    }
    .hiddenGengeMenu:hover {
        display: flex;
    }
}

/* Landscape tablets and medium desktops */
@media (min-width: 992px) and (max-width: 1199px) {
    .wrapHeader {
        position: fixed;
        width: 100%;
        display: flex;
        justify-content: center;
        flex-direction: row;
        background-color: #2f364a;
        height: 3rem;
        padding: 0.2rem;
        z-index: 10000;
        border-bottom: 1px solid black;
    }
    .wrapNav {
        display: flex;
        list-style-type: none;
        justify-content: flex-start;
        padding: 0;
        width: 50%;
    }
    .hiddenHeaderMenu {
        display: none;
    }

    .hiddenGengeMenu {
        display: none;
    }
    .GengeMenu:hover .hiddenGengeMenu {
        display: flex;
        flex-direction: column;
        justify-content: center;
        text-align: center;
        background: rgba(2, 0, 36, 1) 50%;
        border-radius: 10px;
        width: 9rem;
        padding: 1%;
        overflow: hidden;
    }
}

/* Portrait tablets and small desktops */
@media (min-width: 768px) and (max-width: 991px) {
    .wrapHeader {
        position: fixed;
        width: 100%;
        display: flex;
        justify-content: center;
        flex-direction: row;
        background-color: #2f364a;
        height: 3rem;
    }
    .wrapNav {
        display: flex;
        list-style-type: none;
        justify-content: flex-start;
        padding: 0;
        width: 50%;
    }
    .hiddenHeaderMenu {
        display: none;
    }

    .HeaderMenu:hover .hiddenHeaderMenu {
        display: flex;
        flex-direction: column;
        justify-content: center;
        text-align: center;
        background-color: rgba(2, 0, 36, 1);
        border-radius: 5px;
        width: 100%;
    }
    .hiddenGengeMenu {
        display: none;
    }
    .GengeMenu:hover .hiddenGengeMenu {
        display: flex;
        flex-direction: column;
        justify-content: center;
        text-align: center;
        background: rgba(2, 0, 36, 1) 50%;
        border-radius: 10px;
        width: 8rem;
        padding: 1%;
        overflow: hidden;
    }
}

/* Landscape phones and portrait tablets */
@media (max-width: 767px) {
    .hiddenHeaderMenu {
        display: none;
    }

    .HeaderMenu:hover .hiddenHeaderMenu {
        display: flex;
        flex-direction: column;
        justify-content: center;
        text-align: center;
        background-color: #2f364a;
        border-radius: 5px;
        width: 7rem;
    }
}

