.pagination_box {
    display: flex;
    min-width: 320px;
    justify-content: center;
    flex-direction: column;
    margin: 0 auto;
    padding: 1% 0;
}

.pagination_box ul {
    display: flex;
    justify-content: center;
}

.pagination_box li {
    font-size: 1rem;
    color: white;
    margin: 0 1%;
}

.pagination_box li:hover {
    color: white;
    transform: scale(1.2);
    transition: 0.2s linear;
}

@media (min-width: 1200px) {
    .pagination_box {
        margin-bottom: 3.5rem;
    }
}
@media (min-width: 992px) and (max-width: 1199px) {
    .pagination_box {
        margin-bottom: 3.5rem;
    }
}

@media (min-width: 768px) and (max-width: 991px) {
    .pagination_box {
        margin-bottom: 3.5rem;
    }
}
