.wrapGenre {
    display: flex;
    justify-content: center;
    text-align: left;
    width: 100%;
    font-size: 0.9rem;
}
.containerGenre {
    display: flex;

    flex-direction: column;
    color: white;
    list-style: none;
    padding: 1%;
}

.itemGenre {
    margin: 0.3rem auto;
    cursor: pointer;
}

.itemGenre:hover {
    color: #2f364a;
    text-shadow: 0 1px 0 rgb(255 255 255 / 10%),
        0 0 10px rgb(255 255 255 / 100%);
}

@media (min-width: 1200px) {
    .wrapGenre {
        font-size: 1.1rem;
    }
}
